.card{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    background-color: white;
    border-radius: 5px;
    border: none;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 10px;
    text-align: center;
    color: black;
}

.deleteButton {
    height: 20px;
    width: 20px;
    border-radius: 5px;
    border: none;
    justify-self: right;
}